<template>
  <div class="about">
    <h1>{{ id? '' : '' }}</h1>
    <el-form label-width="100px" @submit.native.prevent="save">
      <!-- <el-form-item label="">
            <el-select v-model="model.parent">     
                <el-option v-for="item in parents" :key="item._id" :label="item.name" :value="item._id"></el-option>      
            </el-select>
      </el-form-item>-->
      <el-tabs type="border-card">
        <el-tab-pane label="">
          <el-form-item label="">
            <el-input v-model="model.name"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input v-model="model.title"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="getAuthHeaders()"
              :show-file-list="false"
              :on-success="res => {$set(model,'avatar',res.url)}"
            >
              <img v-if="model.avatar" :src="model.avatar" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="Banner">
            <el-upload
              class="avatar-uploader"
              :action="uploadUrl"
              :headers="getAuthHeaders()"
              :show-file-list="false"
              :on-success="res => { $set(model,'banner',res.url)}"
            >
              <img v-if="model.banner" :src="model.banner" class="avatar" />
              <i v-else class="el-icon-plus avatar-uploader-icon"></i>
            </el-upload>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.categories" multiple>
              <el-option
                v-for="item of categories"
                :label="item.name"
                :value="item._id"
                :key="item._id"
              ></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model="model.scores.difficult"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model="model.scores.skills"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model="model.scores.attack"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-rate style="margin-top:0.6rem" :max="9" show-score v-model="model.scores.survive"></el-rate>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.items1" multiple>
              <el-option v-for="item of items" :label="item.name" :value="item._id" :key="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-select v-model="model.items2" multiple>
              <el-option v-for="item of items" :label="item.name" :value="item._id" :key="item._id"></el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.usageTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.battleTips"></el-input>
          </el-form-item>
          <el-form-item label="">
            <el-input type="textarea" v-model="model.teamTips"></el-input>
          </el-form-item>
        </el-tab-pane>
        <el-tab-pane label="" name="skills">
          <el-button size="small" @click="model.skills.push({})">
            <i class="el-icon-plus"></i> 
          </el-button>
          <el-row type="flex" style="flex-wrap:wrap">
            <el-col :md="12" v-for="(item,i) in model.skills" :key="i">
              <el-form-item label="">
                <el-input v-model="item.name"></el-input>
              </el-form-item>
              <el-form-item label="" v-model="item.icon">
                <el-upload
                  class="avatar-uploader"
                  :action="uploadUrl"
                  :headers="getAuthHeaders()"
                  :show-file-list="false"
                  :on-success="res => $set(item,'icon',res.url)"
                >
                  <img v-if="item.icon" :src="item.icon" class="avatar" />
                  <i v-else class="el-icon-plus avatar-uploader-icon"></i>
                </el-upload>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.delay"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.cost"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.tips" type="textarea"></el-input>
              </el-form-item>
              <el-form-item>
                <el-button size="small" type="danger" @click="model.skills.pop()"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
               <el-tab-pane label="" name="partners">
          <el-button size="small" @click="model.partners.push({})">
            <i class="el-icon-plus"></i> 
          </el-button>
          <el-row type="flex" style="flex-wrap:wrap">
            <el-col :md="12" v-for="(item,i) in model.partners" :key="i">
              <el-form-item label="">
                <el-select v-model="item.hero" filterable>
                  <el-option v-for="hero in heroes" :key=hero._id :value="hero._id" :label="hero.name">

                  </el-option>
                </el-select>
              </el-form-item>
              <el-form-item label="">
                <el-input v-model="item.description" type="textarea"></el-input>
              </el-form-item>
              
              <el-form-item>
                <el-button size="small" type="danger" @click="model.partners.pop()"></el-button>
              </el-form-item>
            </el-col>
          </el-row>
        </el-tab-pane>
      </el-tabs>
      <el-form-item>
        <el-button type="primary" native-type="submit" style="margin-top:1rem"></el-button>
      </el-form-item>
    </el-form>
  </div>
</template>
<script>
export default {
  props: {
    id: {} //
  },
  data() {
    return {
      categories: [],
      items: [],
      heroes:[],
      model: {
        name: "",
        avatar: "",
        skills: [],
        partners:[],
        scores: {
          difficult: 0
        }
      }
      // parents:[]
    };
  },
  methods: {
    // afterUpload(res) {
    //   // this.$set(this.model,'avatar',res.url) //vue,icon// this.model.icon = res.url 
    //   // this.model.icon = res.url  modelmodelicon,model
    //   // console.log(res);
    //   this.model.avatar = res.url;
    // },
    async save() {
      //    const res =
      // let res
      if (this.id) {
        await this.$http.put(`rest/heroes/${this.id}`, this.model);
      } else {
        await this.$http.post("rest/heroes", this.model);
      }

      this.$router.push("/heroes/list");
      this.$message({
        type: "success",
        message: " "
      });
    },
    async fetch() {
      const res = await this.$http.get(`rest/heroes/${this.id}`);
      // this.model = res.data;  //model,scores 
      this.model = Object.assign({}, this.model, res.data); //this.model,res.datares.data 
    },
    async fetchItems() {
      const res = await this.$http.get(`rest/items`);
      this.items = res.data;
    },
    async fetchCategories() {
      const res = await this.$http.get(`rest/categories`);
      this.categories = res.data;
    },
    // async fetchParents(){
    //     const res = await this.$http.get(`rest/items`)
    //     this.parents = res.data
    // }
     async fetchHeroes() {
      const res = await this.$http.get(`rest/heroes`);
      this.heroes = res.data;
    },
  },
  created() {
    this.id && this.fetch();
    this.fetchCategories();
    this.fetchItems();
    this.fetchHeroes()
    // this.fetchParents()
  }
};
</script>