<template>
  <div class="about">
    <h1></h1>
        <el-table :data="items">
        <el-table-column prop="_id" label="ID" width="220">
        </el-table-column>

        <el-table-column prop="title" label="" width="120">
        </el-table-column>
               <el-table-column
      fixed="right"
      label=""
      width="100">
      <template slot-scope="scope">
        <el-button type="text" size="small" @click="$router.push(`/articles/edit/${scope.row._id}`)"></el-button>
        <el-button type="text" size="small" @click="remove(scope.row)"></el-button>
      </template>
    </el-table-column>
      </el-table>
  </div>
</template>
<script>
export default {
    data(){
        return{
            items:[]
        }
    },
    created(){
        this.fetch()
    },
    methods: {
        async fetch(){
            const res = await this.$http.get('rest/articles')
            this.items = res.data
        },
        async remove(row){
          this.$confirm(`${row.title}`, '', {
          confirmButtonText: '',
          cancelButtonText: '',
          type: 'warning'
        }).then(async () => {
          // const res =
          await this.$http.delete(`rest/articles/${row._id}`)
          this.$message({
            type: 'success',
            message: '!'
          });
          this.fetch()
        })
        }
    },
}
</script>